
let urlId = 1; //图片Id
import axios from "axios";
import { ref } from "vue";
import Axios from "axios";
import { useRouter, useRoute } from "vue-router";

export default {
	name: "roomUpload",

	setup() {
		const input = ref();
		const route = useRoute();
		const url = ref(null);
		//上传图片的url列表
		const urlList = ref([]);
		//上传文件
		const upload = () => {
			input.value.click();
		};

		//文件改变
		const handleFileChange = e => {
			const currentTarget = e.target as HTMLInputElement;
			if (currentTarget.files) {
				const files = Array.from(currentTarget.files);
				console.log("files", files);
				const formData = new FormData();
				formData.append("file", files[0]);
				const token = localStorage.getItem("token");
				axios
					.post("/emulation/uploadFile", formData, {
						headers: {
							"Content-Type": "multipart/form-data",
							Authorization: `Bearer ${token}`
						}
					})
					.then(res => {
						url.value = res.data.data.picName;
						console.log("wmc", url);
						urlList.value.push({ id: urlId, url: res.data.url });
						urlId++;
						console.log(urlList.value);
						console.log(res.data.url);
					});
			}
		};
		//上传图片
		const submit = async () => {
			console.log("@@@@@", url);
			const data = await Axios.post("emulation/createMsg", {
				url: url.value,
				roomId: route.params.id
			});
		};
		return { input, upload, submit, handleFileChange, url, urlList };
	}
};
